import { useContext } from 'react'
import { Link, useNavigate} from 'react-router-dom'
import AuthContext from '../context/AuthContext'
import {useFormik} from "formik"

const Login = () => {
  const navigate = useNavigate()

 
  const {loginUser, user} = useContext(AuthContext)

  
  const handleSubmit = async (valores) => {

    try {
      await loginUser(valores.nickname,valores.password)
      navigate("/dashboard")
      
    
  } catch {

  }
}

const formik = useFormik({
    initialValues:{
        nickname: '',
        password: '',
        level: 'Seller'
    },
    onSubmit: (values) => {
        handleSubmit(values)
    }
})
 

  return (
    <div className="hold-transition login-page">
<div className="login-box">
  <div className="login-logo">
    <a href="../../index2.html"><b>Cloud</b>SALES</a>
  </div>
  
  <div className="card">
    <div className="card-body login-card-body">
      <p className="login-box-msg">Inicia Sesin</p>

      <form onSubmit={formik.handleSubmit} method="post">
        <div className="input-group mb-3">
          <input 
            type="text" 
            id='nickname'
            className="form-control" 
            placeholder="Nickname" 
            value={formik.values.nickname}
            onChange={formik.handleChange}/>
          <div className="input-group-append">
            <div className="input-group-text">
              <span className="fas fa-envelope"></span>
            </div>
          </div>
        </div>
        <div className="input-group mb-3">
          <input 
              type="password"
              id='password'
              className="form-control" 
              value={formik.values.password}
              onChange={formik.handleChange}
              placeholder="Password"/>
          <div className="input-group-append">
            <div className="input-group-text">
              <span className="fas fa-lock"></span>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-8">
            <div className="icheck-primary">
              <input type="checkbox" id="remember"/>
              <label htmlFor="remember"> Recurdame
              </label>
            </div>
          </div>
          
          <div className="col-4">
            <button 
              type="submit" 
              className="btn btn-primary btn-block"
              >Sign In</button>
          </div>
          <p className="mb-0">
        <Link to={{ pathname:'/register'}} className="text-center">Registarse</Link>
      </p>
          
        </div>
      </form>
    </div>
    
  </div>
</div>
</div>


  )
}

export default Login