import { Outlet, Navigate } from "react-router-dom";
import { useContext } from "react";
import AuthContext from "../context/AuthContext";
const RutaPrivada = () => {
  
  let { user } = useContext(AuthContext);
  if (!user ){
    return <Navigate to={'/'} replace />
  
};

return <Outlet/>
}
export default RutaPrivada